/* home slider */
.homeSliderLeft {
  right: 0%;
  animation: rightLeftTrigger infinite 950ms ease-in-out;
}

.homeSliderRight {
  left: 0%;
  animation: leftRightTrigger infinite 950ms ease-in-out;
}

@keyframes leftRightTrigger {
  0% {
    left: 0%;
  }

  50% {
    left: 40%;
  }

  0% {
    left: 0%;
  }
}

@keyframes rightLeftTrigger {
  0% {
    right: 0%;
  }

  50% {
    right: 40%;
  }

  0% {
    right: 0%;
  }
}

/* joomla */
.jm-button-dark {
  border: 1px solid transparent;
  text-align: center;
  background: #242424 !important;
  color: #fdfdfd !important;
  transition: border 100ms, background 400ms ease 0s, color 400ms ease 0s !important;
}

.jm-button-dark:hover {
  background-color: #d93 !important;
  color: #1e1e1e !important;
  border-color: #d93;
}

.jm-button-gray {
  background-color: #525252 !important;
  color: #fff !important;
}

.jm-button-light-gray {
  background-color: #525252 !important;
  color: #fff !important;
}

@media screen and (max-width: 768px) {
  .jm-button-light-gray {
    background-color: #a9a9a9 !important;
  }
}

.jm-button-green {
  background-color: #39884f !important;
  color: #fff !important;
}

.jm-button-green:is(:hover, :focus),
.jm-button-light-gray:is(:hover, :focus) {
  background-color: #d93 !important;
  color: #1e1e1e !important;
}

.jm-button-red {
  background-color: #b92c2c !important;
  color: #fff !important;
}

.jm-button-red:is(:hover, :focus) {
  background-color: #d93 !important;
  color: #1e1e1e !important;
}

.jm-button-light {
  border: 1px solid transparent;
  padding: 0;
  text-align: center;
  background-color: #d93 !important;
  color: #1e1e1e !important;
  transition: border 100ms, background 400ms ease 0s, color 400ms ease 0s !important;
}

.jm-button-light:hover {
  background: #1e1e1e !important;
  color: #d93 !important;
  border-color: #1e1e1e;
}

.jm-input-dark {
  position: relative !important;
  background: #1e1e1e !important;
  color: #c1c1c1 !important;
  border: 1px solid #242424 !important;
  display: block;
  font-weight: 400;
  font-style: oblique;
  -webkit-transition: all 400ms ease 0s;
  transition: all 400ms ease 0s;
}

.jm-input-dark:focus,
.jm-input-dark:focus-visible {
  border: 1px solid !important;
  border-color: #dd9933 !important;
  background: #ffffff !important;
  color: #242424 !important;
  box-shadow: none !important;
}

.jm-input-dark::placeholder {
  color: #c1c1c1;
}

.jm-menu-link-header {
  width: max-content;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.15rem;
  text-transform: uppercase;
  font-size: 15px;
  font-weight: 600;
  transition: all 350ms;
}

.jm-menu-link-header:hover {
  color: #dd9933;
}

.jm-menu-link-footer {
  width: max-content;
  cursor: pointer;
  position: relative;
  left: 0;
  display: flex;
  align-items: center;
  gap: 0.15rem;
  transition: all 350ms;
  color: #999;
}

.jm-menu-link-footer:hover {
  left: 10px;
  color: #dd9933;
}

.jm-menu-link-side {
  color: #f2f2f2;
  width: max-content;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.15rem;
  transition: all 350ms;
}

.jm-menu-link-side:hover {
  color: #dd9933;
}

.jm-menu-link-extended {
  width: max-content;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.15rem;
  color: #dd9933;
  font-weight: 900;
  font-style: oblique;
  text-transform: none;
}

/* livezilla chat overlay */
#lz_overlay_wm,
#lz_overlay_chat {
  bottom: 10px !important;
}

.jm-swich {
  margin: 0 15px;
}

.jm-swich .css-p27qcy {
  width: 56px;
  height: 20px;
  background: #eaeaea;
  align-items: center;
  padding: 3px 4px;
}

.jm-swich .css-p27qcy[data-checked] .css-7roig {
  background: #dd9933;
  transform: translateX(2.5rem);
}

/* profile update: resume */
.resume-document canvas {
  width: 100% !important;
  max-width: 620px;
  height: auto !important;
}

.resume-download-btn {
  display: none !important;
}

@media screen and (max-width: 620px) {
  .resume-wrapper {
    display: none !important;
  }

  .resume-download-btn {
    display: flex !important;
  }

}

/* external */
.lz_form_box::placeholder {
