.wrapper {
  position: relative;
  overflow: hidden;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000000;
    opacity: 0.85;
  }
}

.social {
  font-size: 14px;
  width: 30px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  background-color: #242424;
  display: block;
  color: #ffffff;
  border: 1px solid transparent;
  transition: background 0.4s ease 0s, color 0.4s ease 0s;
  border-radius: unset;
  &:hover {
    background-color: #dd9933;
    color: #242424;
    border: 1px solid #dd9933;
  }
}

.download {
	@media (max-width: 475px) {
		max-width: 288px;
	}
}

.subscribe_icon {
	min-width: 1.35rem;
}